import React, { Component } from 'react';
import axios from 'axios'
import { DragDrop, StatusBar } from '@uppy/react';
import UppyUploader from './uppyLoader'
import UppyUpload from './uppyUpload'
import { VIDEO_COMPRESS } from '../../../../actions/api'
import { bytesToSize, tb_onsite_btn_disabled } from '../../../../utils'

class Uploads extends Component {
    state = {
        isLoading: false,
        selectedFile: null,
        selectedUrl: null,
        type: null,
        thumb_file: "",
        fileSize: false,
        filemaxSize: 0,
        throwError: false,
        videoPath: "",
        fileSizeText: "",
        ext: null, size: 0, 
        fileName: null,
        mediaHeight: 0,
        mediaWidth: 0
    }
    beforeFileAdded = (currentFile, files) => {
        // console.log("before update", currentFile, files)

        tb_onsite_btn_disabled(1)
        const { size, sizeText } = bytesToSize(currentFile.size);
        //console.log("bytesToSize", size, sizeText)
        if (['GB', 'TB'].includes(sizeText)) {
            this.setState({ fileSize: true, fileSizeText: `${size}${sizeText}` }, () => setTimeout(() => this.setState({ fileSize: false, fileSizeText: "" }), 2000))
            tb_onsite_btn_disabled(2)

        }
        else if (sizeText == "MB") {  

            const fileType = currentFile.meta.type.includes('image') ? 4 : 5;
            const maxuploadSize  = fileType==4 ? 15 : 100;

            if (size >= maxuploadSize) {
                this.setState({ type : fileType,filemaxSize : maxuploadSize, fileSize: true, fileSizeText: `${size}${sizeText}` }, () => setTimeout(() => this.setState({ fileSize: false, fileSizeText: "" }), 5000))
                tb_onsite_btn_disabled(2)
                return false
            }
        }
    }

    beforeUpload = (files) => {
        const fileKeys = Object.keys(files);

        if (fileKeys && fileKeys.length > 0 && files[fileKeys[0]] && files[fileKeys[0]].meta && files[fileKeys[0]].meta.type.includes("video")) {
            try {
                const { onsite_token } = this.props;
                this.setState({ isLoading: true })
                const updateFiles = files[fileKeys[0]];
                //console.log("files", updateFiles)
                const data = new FormData();
                data.append("file", updateFiles.data);
                data.append("onsite_token", onsite_token);
                axios.post(VIDEO_COMPRESS, data).then((response) => {
                    const { responseData } = response.data;
                    if (responseData && responseData.length > 0) {
                        this.setState({
                            videoPath: responseData[0].sscode, isLoading: false, type: 5, selectedFile: responseData[0].s3Url, selectedUrl: responseData[0].s3Url, thumb_file: responseData[0].s3ThumbUrl, ext: updateFiles.extension, size: updateFiles.size, fileName: updateFiles.name, mediaHeight: responseData[0].height,
                            mediaWidth: responseData[0].width
                        }, () => this.onUpdateLocalState())
                        tb_onsite_btn_disabled(2)
                    } //console.log('ccc');
                    //console.log("VIDEO_COMPRESS response", response)
                }).catch((error) => {
                    this.setState({ isLoading: false, throwError: true }, () => setTimeout(() => this.setState({ throwError: false }), 2000))
                    this.uppyUploader.uppy.reset()
                    tb_onsite_btn_disabled(2)
                })
            }
            catch (ex) {
                this.setState({ isLoading: false, throwError: true }, () => setTimeout(() => this.setState({ throwError: false }), 2000))
                this.uppyUploader.uppy.reset()
                tb_onsite_btn_disabled(2)
            }
            return false;
        }
    }
    uppyUploader = new UppyUpload(this.beforeFileAdded, this.beforeUpload)
    componentDidMount() {

        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {   

            const height = result.meta.orientation && result.meta.orientation == "Rotate 90 CW" ?  result.meta.width  : result.meta.height;
            const width  = result.meta.orientation && result.meta.orientation == "Rotate 90 CW" ?  result.meta.height : result.meta.width;

            this.setState({
                type: result.mime.includes("video") ? 5 : 4, selectedFile: result.id, selectedUrl: `https://cloud.taggbox.com/onsiteupload/images/${result.id}`, ext: result.ext, size: result.size, fileName: result.name, mediaHeight: height,
                mediaWidth: width
            }, () => this.onUpdateLocalState())
            tb_onsite_btn_disabled(2)
        })
        this.uppyUploader.uppy.on('file-removed', (file, reason) => {
            tb_onsite_btn_disabled(2)
        })

    }

    onUpdateLocalState = () => {
        const { addPostImage, isActiveIndex } = this.props;
        const { selectedUrl, type, thumb_file, videoPath, ext, size, fileName, mediaHeight, mediaWidth } = this.state;
        addPostImage(selectedUrl, type === 5 ? thumb_file : selectedUrl, videoPath, type, isActiveIndex, ext, size, fileName, mediaHeight, mediaWidth)
        this.setState({
            selectedFile: null,
            selectedUrl: null,
            type: null,
            thumb_file: "",
            fileSize: false,
            fileSizeText: "",
            videoPath: "",
            ext: null, size: 0, fileName: null, mediaHeight: 0, mediaWidth: 0
        }, () => this.uppyUploader.uppy.reset())
    }

    render() {
        const { isLoading, fileSize, throwError, fileSizeText, filemaxSize, type } = this.state;
        return <>
            <DragDrop

                note="Video Size : Max 100MB | Photo Size : Max 15MB | Max 20 Posts"
                uppy={this.uppyUploader.uppy}
                allowMultipleFiles={false}

                locale={{
                    strings: {
                        // Text to show on the droppable area.
                        // `%{browse}` is replaced with a link that opens the system file selection dialog.
                        dropHereOr: 'Drag a video / image to upload (optional)',
                        // Used as the label for the link that opens the system file selection dialog.
                        browse: '',
                    },
                }}
            />

            <div className="tb_onsite_uploader_status">
                <StatusBar
                    uppy={this.uppyUploader.uppy}
                    hideUploadButton
                    hideAfterFinish={false}
                    showProgressDetails
                />
                {isLoading ? <UppyUploader /> : null}
                {fileSize ? <div className="tb_onsite_uploader_limit_error submit_limit_size" style={{ marginTop: 5, display: `flex`, alignItems: `center` }}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                        <g id="Stockholm-icons-/-Code-/-Info-circle" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                            <rect id="bound" x="0" y="0" width="24" height="24" />
                            <circle id="Oval-5" fill="#db4b20" opacity="0.3" cx="12" cy="12" r="10" />
                            <rect id="Rectangle-9" fill="#db4b20" x="11" y="10" width="2" height="7" rx="1" />
                            <rect id="Rectangle-9-Copy" fill="#db4b20" x="11" y="7" width="2" height="2" rx="1" />
                        </g>
                    </svg>
                    File Size ({fileSizeText}) Limit Exceeded { type==4 ? 'Image' : 'Video'} Size : Max {`${filemaxSize}MB`}
                </div> : null}
                {throwError ? <div className="tb_onsite_uploader_limit_error submit_limit_size" style={{ marginTop: 5, display: `flex`, alignItems: `center` }}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                        <g id="Stockholm-icons-/-Code-/-Info-circle" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                            <rect id="bound" x="0" y="0" width="24" height="24" />
                            <circle id="Oval-5" fill="#db4b20" opacity="0.3" cx="12" cy="12" r="10" />
                            <rect id="Rectangle-9" fill="#db4b20" x="11" y="10" width="2" height="7" rx="1" />
                            <rect id="Rectangle-9-Copy" fill="#db4b20" x="11" y="7" width="2" height="2" rx="1" />
                        </g>
                    </svg>
                    Something went wrong , Please try again.
                </div> : null}
            </div>

        </>
    }
}

export default Uploads;