import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import OnSiteUpload from "./OnSiteUpload";


class UserComponents extends PureComponent {
  render() {
    const { modalPop, appData } = this.props
    return (<>
      {modalPop.onSiteIsShowPopUp && appData.wall && Object.keys(appData.wall.Wall).length > 0 ? <OnSiteUpload wall={appData.wall} wallId={appData.wall.Wall.id} feeds={appData.feeds} /> : ''}
    </>
    );
  }
}

const mapStateToProps = state => {
  return {
    modalPop: state.modalPop,
    appData: state.appData,
    loaderData: state.loaderData
  }
}

export default connect(mapStateToProps)(UserComponents);

